import {Component} from "react";
// import Drawer from "../menu";
import React from "react";
import red from "@material-ui/core/colors/red";
import {Link} from "react-router-dom";
import orange from "@material-ui/core/colors/orange";
import yellow from "@material-ui/core/colors/yellow";
import teal from "@material-ui/core/es/colors/teal";
import blue from "@material-ui/core/colors/blue";
import indigo from "@material-ui/core/colors/indigo";
import deepPurple from "@material-ui/core/colors/deepPurple";
import List from '@material-ui/core/List';
// import Divider from '@material-ui/core/Divider';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import Avatar from '@material-ui/core/Avatar';
import Typography from '@material-ui/core/Typography';
import ListItemIcon from "@material-ui/core/ListItemIcon";

import Assessment from '@material-ui/icons/Assessment';
import Gavel from '@material-ui/icons/Gavel';
import Done from '@material-ui/icons/Event';
import Fastfood from '@material-ui/icons/Fastfood';
import Opacity from '@material-ui/icons/Opacity';
import Snooze from '@material-ui/icons/Snooze';
import Spa from '@material-ui/icons/Spa';

const styles = {
    grow: {
        flexGrow: 1
    },
    menuButton: {
        marginLeft: -12,
        marginRight: 20,
    },
    avatarBig: {
        margin: 10,
        width: 60,
        height: 60,
    },
    avatar: {
        margin: 10
    }
}

class DrawerTest extends Component {
    constructor(props) {
        super(props);
        this.state = {
            selTest: null
        };
    }
    componentDidMount() {
        this.setState({
            selTest: this.props.selTest
        })
        console.log(this.props.selTest);
    }
    render() {
        return (
            <div style={{width: 250}}>
                <Avatar style={styles.avatarBig} color="red">H</Avatar>
                <div style={{marginLeft: 10}}>
                    <Typography variant="h5" color="inherit"  style={styles.grow}>Alex Peresta</Typography>
                    <Typography variant="caption" fontWeight="fontWeightLight" color="default" paragraph={true} style={styles.grow}>alexperesta@gmail.com</Typography>

                </div>
                <List>
                    <ListItem button style={{color: red[200]}} selected={this.state.selTest === "Dashboard"} component={Link} from="" to="/dashboard">
                        <ListItemIcon><Assessment style={{color: red[500]}}></Assessment></ListItemIcon>
                        <ListItemText primary="Dashboard" />
                    </ListItem>
                    <ListItem button style={{color: orange[200]}} selected={this.state.selTest === "Training"} component={Link} to="/training">
                        <ListItemIcon><Gavel style={{color: orange[500]}}></Gavel></ListItemIcon>
                        <ListItemText primary="Training" />
                    </ListItem>
                    <ListItem button style={{color: yellow[200]}} selected={this.state.selTest === "Notepad"} component={Link} to="/notepad">
                        <ListItemIcon><Done style={{color: yellow[600]}} ></Done></ListItemIcon>
                        <ListItemText primary="Notepad" />
                    </ListItem>
                    <ListItem button style={{color: teal[200]}} selected={this.state.selTest === "Nutrition"} component={Link} to="/nutrition">
                        <ListItemIcon><Fastfood style={{color: teal[500]}} ></Fastfood></ListItemIcon>
                        <ListItemText primary="Nutrition"  />
                    </ListItem>
                    <ListItem button style={{color: blue[200]}} selected={this.state.selTest === "Hydration"} component={Link} to="/hydration">
                        <ListItemIcon><Opacity style={{color: blue[500]}} ></Opacity></ListItemIcon>
                        <ListItemText primary="Hydration" />
                    </ListItem>
                    <ListItem button style={{color: indigo[200]}} selected={this.state.selTest === "Sleep"} component={Link} to="/sleep">
                        <ListItemIcon><Snooze style={{color: indigo[500]}} ></Snooze></ListItemIcon>
                        <ListItemText primary="Sleep" />
                    </ListItem>
                    <ListItem button style={{color: deepPurple[200]}} selected={this.state.selTest === "Meditation"} component={Link} to="/meditation">
                        <ListItemIcon><Spa style={{color: deepPurple[500]}} ></Spa></ListItemIcon>
                        <ListItemText primary="Meditation" />
                    </ListItem>
                </List>
            </div>
        );
    }
}



export default DrawerTest;