import React, { Component } from 'react';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import Grid from '@material-ui/core/Grid';
import BottomNavigation from '@material-ui/core/BottomNavigation';
import BottomNavigationAction from '@material-ui/core/BottomNavigationAction';
import RestoreIcon from '@material-ui/icons/Restore';
import AddBox from '@material-ui/icons/AddBox';
import AddIcon from '@material-ui/icons/Add';
import Fade from '@material-ui/core/Fade';
import Button from '@material-ui/core/Button';
import TextField from "@material-ui/core/TextField";
import Fab from "@material-ui/core/Fab";


const styles = {
    Paper: {
        padding: "20px",
        marginTop: "20px",
        marginBottom: "10px"
    },
    footer: {
        position: "fixed",
        bottom: 0,
        width: "100%"
    },
    textField: {
        width: "40%"

    },
    cont: {
        marginTop: 50
    },
    svg: {
        position: "absolute",
        bottom: 0,
        zIndex: 100
    },
    svgContainer: {
        // position: "relative",
        // top: 0,
        width: "100%",
        height: "100%",
        // zIndex: 10,
    },
    svgSecond: {
        position: "absolute",
        bottom: 0,
        zIndex: 10
    },
    separator: {
        width: "100%",
        height: 400,
        zIndex: 80,
        position: "absolute",
        transition: "all 0.4s"
    },
    button: {
        width: "35%",
        height: "10vh"
    },

}

class Hydration extends Component {
    constructor(props) {
        super(props);
        this.state = {
            checked: false,
            percentage: "100%",
            perc: 100,
            humanFill: "#2196f3"
        };
        this.handleClick = this.handleClick.bind(this);
        this.handleChange = this.handleChange.bind(this);
        this.sendTheme = this.sendTheme.bind(this);
        this.addWaterPercentage = this.addWaterPercentage.bind(this);
        this.handle100 = this.handle100.bind(this);
        this.handle250 = this.handle250.bind(this);
        this.handle500 = this.handle500.bind(this);
        this.handle1L = this.handle1L.bind(this);
        this.handleCustom = this.handleCustom.bind(this);
        this.handleCustomChange = this.handleCustomChange.bind(this);
    }

    componentDidMount() {
        this.setState({
            dataToDisplay: [
            ],
            checked: true
        })
        this.sendTheme();
    }
    sendTheme(){
        this.props.callBackFromParent("blue");
    }
    handleClick(){

    }
    addWaterPercentage(amount){
        let newValue = this.state.perc - (amount / 2500)* 100;
        if(newValue <= 0){
            this.setState({
                perc: 0,
                percentage: "0%",
                humanFill: "#ffea00"
            })
        }else{
            this.setState({
                perc: newValue,
                percentage: newValue + "%",
                humanFill: "#2196f3"
            })
        }

    }
    handle100(){
        this.addWaterPercentage(100);
    }
    handle250(){
        this.addWaterPercentage(250);
    }
    handle500(){
        this.addWaterPercentage(500);
    }
    handle1L(){
        this.addWaterPercentage(1000);
    }
    handleCustomChange(event){
        let newValue = event.target.value;
        this.setState({
            customChange: newValue
        })
    }
    handleCustom(){
        if(this.state.customChange === ""){

        }else{
            this.addWaterPercentage(this.state.customChange);
            this.setState({
                customChange: 0
            })
        }
    }
    handleChange(event){


    }
    render() {
        return (
            <Fade in={this.state.checked}>
                <section id="Water" >
                        <Grid container justify="center">
                            <Grid item xs={12} sm={10} md={8}>
                                <Paper style={styles.Paper}>
                                    <Grid container justify="center" style={{padding: "25px 0"}}>
                                        <Grid item xs={12} md={6}>
                                            <Grid container justify="center">
                                                <div className="containerTest" style={{width: "200px", height: "350px", position: "relative"}}>
                                                    <svg xmlns="http://www.w3.org/2000/svg" width="180%" height="100%" viewBox="0 0 220 210" style={{position: "absolute", top: 0}}>
                                                        <path fill={this.state.humanFill} d="M97.2.4s7.8-2.5 10.9 5c0 0 11.6 12.9 12.9 22.2 0 0 .4 2.8-2.3 4.4-2 1-4.9 3.9-6.7 6.2 0 0-6.3 7.8-17.6 7.8 0 0-1.5-.2-4.6 7.2-1.6 3.7-6 9.3-9.3 13.7a60 60 0 0 0-5.3 11.6c-1.5 4.5-2 15.7-.3 18.8 0 0 5.1 12.3 6.5 17.7.1.3 2.5 7.5 1.3 17.7-.1 1-.8 5-1 7.2a36 36 0 0 0 1 13.3c1.2 3 4.8 5.7 5.9 13.5 1 7.6-1.2 18.5-.4 25.1a15 15 0 0 0 9.3 12.4s6.7 1.6 3.7 3.9c0 0-7.8.3-12.3-1.4 0 0-7.8.6-8.8-2.3 0 0 0-2.2.9-4 0 0-1.7-11.3-7.3-18.8-4-5.3-4.9-7.5-2.8-26.7 0 0-6.4-4-10.1-18.5l-4.1-17s-9.8 20.4-9 30.7c0 0 4.7 10.5 5.3 14.5.5 3.4.4 8.9-1 13.7-1.5 5 1.7 16.6 2.7 20.5 0 0 2.5 6.8-2.4 6.7 0 0-8.4-1-11 .9 0 0-9.7 1.2-11.7.3 0 0-3.2-1.2 2.2-3.6 0 0 5.7-.2 7.7-3.8 0 0 2.6-3 3.4-3.5 0 0-3.5-8.8-7-21.6-1.2-4.9-2.1-16.2-3-21.2-.4-3.1 1.2-10.5.2-13.5-2.6-8-6.6-14.2-.3-33.5l5.4-12.7a43.8 43.8 0 0 0 0-26.7s-5.7-8.2-6-16c0 0 .6-5.2-7.7-5 0 0-6.2 1.3-16.5-8.2l-6-4.5s-3.2-1.6.1-10.4c0 0 5.8-11.1 9-14.4l2.1-4.5s2-5 10.3-2.9c0 0 4.8.7 4 3.5 0 0 1.2 9-1.6 7.9 0 0-1.4-.7-1.7-2.6 0 0-4.2-.2-5.1-1.4 0 0-2.2 6.9-3.3 8 0 0 1 9.9.1 11.9 0 0 8.8-4.6 14.4 3.4 0 0 10.7-3.3 15 2.5 0 0 7.4 5.6 8.5-1.1l-.1-4s-1-2.4-1.3-4.2c0 0-1.8 1.2-2.4-3.8 0 0-1.1-3.3 1.1-2.8 0 0-3.7-9.8 6.6-12.6 0 0 13.5-3 12.3 11.7 0 0 4.9-1.2 0 7.4 0 0-1.4-.4-1.2.3.2.4.5 4.4.5 4.4s.3 6.5 5.8 5.4c0 0 10-7 14.8-3 0 0 6.3-8 14.8-3.8 0 0 2.7.5 3.5-1.9 0 0-5.3-4.3-4-16 0 0-.5-4.2-2.4-3.2L100 7s-.4 3-5.9 2.8c0 0 .2 1 0 1.2-1 .8-3.8.6-4.8 0-2.2-1.3-2.5-7.8 2.7-9.3L97.2.4z"/>
                                                    </svg>
                                                    <svg width="100%" height="100%" style={{position: "absolute", top: 0}}>
                                                        <rect width="100%" height="100%" fill="white" style={{transition: "all 0.3s", height: this.state.percentage}}/>
                                                    </svg>
                                                    <svg xmlns="http://www.w3.org/2000/svg" width="180%" height="100%" viewBox="0 0 220 210" style={{position: "absolute", top: 0}}>
                                                        <path fill="transparent" stroke="#bdbdbd" strokeWidth="2"  d="M97.2.4s7.8-2.5 10.9 5c0 0 11.6 12.9 12.9 22.2 0 0 .4 2.8-2.3 4.4-2 1-4.9 3.9-6.7 6.2 0 0-6.3 7.8-17.6 7.8 0 0-1.5-.2-4.6 7.2-1.6 3.7-6 9.3-9.3 13.7a60 60 0 0 0-5.3 11.6c-1.5 4.5-2 15.7-.3 18.8 0 0 5.1 12.3 6.5 17.7.1.3 2.5 7.5 1.3 17.7-.1 1-.8 5-1 7.2a36 36 0 0 0 1 13.3c1.2 3 4.8 5.7 5.9 13.5 1 7.6-1.2 18.5-.4 25.1a15 15 0 0 0 9.3 12.4s6.7 1.6 3.7 3.9c0 0-7.8.3-12.3-1.4 0 0-7.8.6-8.8-2.3 0 0 0-2.2.9-4 0 0-1.7-11.3-7.3-18.8-4-5.3-4.9-7.5-2.8-26.7 0 0-6.4-4-10.1-18.5l-4.1-17s-9.8 20.4-9 30.7c0 0 4.7 10.5 5.3 14.5.5 3.4.4 8.9-1 13.7-1.5 5 1.7 16.6 2.7 20.5 0 0 2.5 6.8-2.4 6.7 0 0-8.4-1-11 .9 0 0-9.7 1.2-11.7.3 0 0-3.2-1.2 2.2-3.6 0 0 5.7-.2 7.7-3.8 0 0 2.6-3 3.4-3.5 0 0-3.5-8.8-7-21.6-1.2-4.9-2.1-16.2-3-21.2-.4-3.1 1.2-10.5.2-13.5-2.6-8-6.6-14.2-.3-33.5l5.4-12.7a43.8 43.8 0 0 0 0-26.7s-5.7-8.2-6-16c0 0 .6-5.2-7.7-5 0 0-6.2 1.3-16.5-8.2l-6-4.5s-3.2-1.6.1-10.4c0 0 5.8-11.1 9-14.4l2.1-4.5s2-5 10.3-2.9c0 0 4.8.7 4 3.5 0 0 1.2 9-1.6 7.9 0 0-1.4-.7-1.7-2.6 0 0-4.2-.2-5.1-1.4 0 0-2.2 6.9-3.3 8 0 0 1 9.9.1 11.9 0 0 8.8-4.6 14.4 3.4 0 0 10.7-3.3 15 2.5 0 0 7.4 5.6 8.5-1.1l-.1-4s-1-2.4-1.3-4.2c0 0-1.8 1.2-2.4-3.8 0 0-1.1-3.3 1.1-2.8 0 0-3.7-9.8 6.6-12.6 0 0 13.5-3 12.3 11.7 0 0 4.9-1.2 0 7.4 0 0-1.4-.4-1.2.3.2.4.5 4.4.5 4.4s.3 6.5 5.8 5.4c0 0 10-7 14.8-3 0 0 6.3-8 14.8-3.8 0 0 2.7.5 3.5-1.9 0 0-5.3-4.3-4-16 0 0-.5-4.2-2.4-3.2L100 7s-.4 3-5.9 2.8c0 0 .2 1 0 1.2-1 .8-3.8.6-4.8 0-2.2-1.3-2.5-7.8 2.7-9.3L97.2.4z"/>
                                                    </svg>
                                                </div>
                                            </Grid>
                                            <Grid container justify="center">
                                                <Typography style={{textAlign: "center", marginTop: 15}} variant="h5">
                                                    {this.state.perc}% left!
                                                </Typography>
                                            </Grid>
                                        </Grid>
                                        <Grid item xs={12} md={6}>
                                            <Grid container justify="center" alignItems="center">
                                                <Grid container justify="space-around" style={{marginTop: 20}}>
                                                    <Button variant="contained" size="large" color="primary" style={styles.button} onClick={this.handle100}>
                                                        100ML
                                                    </Button>
                                                    <Button variant="contained" size="large" color="primary" style={styles.button} onClick={this.handle250}>
                                                        250ML
                                                    </Button>
                                                </Grid>
                                                <Grid container  justify="space-around" style={{marginTop: 50}}>
                                                    <Button variant="contained" size="large" color="primary" style={styles.button} onClick={this.handle500}>
                                                        500ML
                                                    </Button>
                                                    <Button variant="contained" size="large" color="primary" style={styles.button} onClick={this.handle1L}>
                                                        1L
                                                    </Button>
                                                </Grid>
                                                <Grid container  justify="space-around" style={{marginTop: 30}}>
                                                    <TextField
                                                        id="waterMl"
                                                        name="ml"
                                                        type="number"
                                                        label="Enter ml"
                                                        style={styles.textField}
                                                        margin="dense"
                                                        variant="outlined"
                                                        onChange={this.handleCustomChange}
                                                        ></TextField>
                                                </Grid>
                                                <Grid container  justify="space-around">
                                                    <Fab color="primary" aria-label="Add" onClick={this.handleCustom}>
                                                        <AddIcon />
                                                    </Fab>
                                                </Grid>
                                            </Grid>
                                        </Grid>
                                    </Grid>
                                </Paper>
                            </Grid>
                        </Grid>
                        <Paper style={styles.footer}>
                            <BottomNavigation>
                                <BottomNavigationAction label="Recent" icon={<RestoreIcon />} />
                                <BottomNavigationAction label="Favorites" icon={<AddBox />} />
                            </BottomNavigation>
                        </Paper>
                </section>
            </Fade>
        );
    }
}



export default Hydration;